#site-container {
  min-height: 100vh;
}
#site-content {
  flex: 1;
}
.cart-container {
  border: 1px solid #ccc;
}
.cart-row {
  padding: 10px;
  border-bottom: 1px solid #ccc;
}

.cart-row:last-child {
  border: none;
}
.cart-left {
  display: flex;
  align-items: center;
}
.cart-left img {
  max-width: 100px;
}

.cart-left span {
  margin-left: 10px;
  display: inline-block;
}
